import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import newsAllItemFetch from 'redux/actions/news';

function News({ newsAllItemFetch, news }) {

    useEffect(() => {
        newsAllItemFetch()
    }, [newsAllItemFetch]);

    return (
        <main className="main">
            <div className="container">
                <div className="news">
                    <h1>Новости</h1>
                    {news && news.map(({ id, title, body }) => (
                        <article key={id}>
                            <h2>{title}</h2>
                            <p>{body}</p>
                        </article>
                    ))}
                </div>
            </div>
        </main>
    );
}

const mapStateToProps = (state) => ({
    news: state.news.data,
});

const mapDispatchToProps = (dispatch) => ({
    newsAllItemFetch: () => dispatch(newsAllItemFetch()),
});

export default connect(mapStateToProps, mapDispatchToProps)(News);